@import url('https://fonts.googleapis.com/css?family=Muli');

body {
  /*font-family: 'Baloo Tammudu', cursive;*/
  font-family: 'Muli', sans-serif;
}

/*@media (max-width: 500px){
  #app{
    width: 400px;
    margin: auto;
  }
}*/
/*for Desktop*/
#app{
  max-width: 700px;
  margin: auto;
}

.header {
  font-size: 20px;
  margin: 20px 0px 20px 0px;
  text-align: center;

}

.coinheader {
  text-align: center;
}

.buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  /*flex-grow: 1;*/
}

.btn {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-bottom: 20px;
  height: 40px;
  border-radius: 5px;
  color: white;
  background-color: #337AB7;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.coinimage {
  width: 50px;
  height: 50px;
  margin: auto;
}


.coindetails {
  background: #eff2f4;
  border: 1px solid blue;
  margin: 10px 0px 10px 0px;
  padding: 10px;
  z-index: 10;
}

.coinpricedetails {
  margin: 10px;
}

.coinsection1 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: -1;
}

.chartdiv {
  margin-top: 20px;
  width: 100%;
  height: 400px;
}

.bold {
  font-weight: 900;
}

.comparecoinsheader {
  text-align: center;
  margin: 10px;
}

.chartexplain {
  text-indent: 30px;
  margin-top: 30px;
}

.compareoptions {
  width: 400px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;

}

.comparecoinimage {
  width: 25px;
  height:25px;
  /*margin: 1px;*/
}

.compareselectedoptions {
  height: 30px;
  /*padding: 5px;*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
@import url('https://fonts.googleapis.com/css?family=Share+Tech+Mono');

.topnavbarcontainer {
  font-size: 0.9em;
  font-family: 'Share Tech Mono', monospace;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: black;
  color: white;
  height: 60px;
  border-radius: 2px;
  -ms-flex-align: center;
      align-items: center;
}

.menu:nth-of-type(1):hover{
  border: none;
  cursor: default;
}

.menu {
  /*border-left:  1px solid white;*/
  padding: 5px;
  height: 80%;
  width: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  /*text-decoration: none;*/
}

.menu-image {
  /*align-self: flex-start;*/
  width: 50px;

}



.menu:hover {
  /*text-decoration: none;*/
  cursor: pointer;
  color: red;
  /*background: green;*/
  border: 1px solid red;
}

.footer {
  font-size: 0.9em;
  display: -ms-flexbox;
  display: flex;
  font-family: 'Share Tech Mono', monospace;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  background-color: black;
  color: white;
  height: 60px;
}



